<template>
  <div>
    <div class="under-cell-wapper">
      <div class="right-abslute-down">
        <span class="icon-jiantou-copy iconfont" @click="lookShopsHandle"></span>
      </div>
      <div class="cell-info">
        <div class="cell-info-left">
          <div class="img-container">
            <img v-lazy="cellData.headimgurl" class="img" />
          </div>
        </div>
        <div class="cell-info-right">
          <p class="name">{{cellData.username}}</p>
          <p class="icon-phone iconfont"><span class="under-phone">{{telephone}}</span></p>
          <p class="relieve" @click="relieveCall">解除绑定</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { mapState } from "vuex";

export default {
  name: "agency-under-cell",
  data() {
    return {
      currentValue: 0
    };
  },
  props: {
    cellData: {
      type: Object,
      default: function() {
        return {
          headimgurl: "http://www.jubenxiong.com/aml/img/nx_1515661463680.png",
          phone: "未绑定",
          username: "L先生",
          role: 1,
          userid: "",
          salescommission: 0
        };
      }
    },
    isReplace: false
  },
  components: {},
  activated() {},
  mounted() {},
  methods: {
    relieveCall() {
      if (this.$parent.$parent.relieveCall) {
        this.$parent.$parent.relieveCall(this.cellData);
      } else this.$parent.$parent.$parent.relieveCall(this.cellData);
    },
    lookShopsHandle() {
      let titleName = this.cellData.username + " - 客户列表";
      if (this.isReplace)
        this.$router.replace({
          name: "userlist",
          params: { titleName: titleName, userid: this.cellData.userid }
        });
      else
        this.$router.push({
          name: "userlist",
          params: { titleName: titleName, userid: this.cellData.userid }
        });
    }
  },
  computed: {
    ...mapState(["openid", "path", "userid"]),
    telephone: function() {
      console.log("this.phone=" + this.phone);
      if (this.cellData.phone && this.cellData.phone != "") {
        return this.cellData.phone;
      } else return "未绑定";
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.under-cell-wapper {
  width: 100%;
  position: relative;
  height: 78px;

  .cell-info {
    height: 100%;
    font-size: 0px;
    // background-color: #fff;
    display: flex;
    align-items: center;

    .cell-info-left {
      flex 0 1 72px;
      text-align: center;

      .img-container {
        display: inline-block;
        width: 60px;
        height: 60px;

        .img {
          width: 100%;
          height: 100%;
          border-radius: 50%;
          position: relative;
        }
      }
    }

    .cell-info-right {
      flex 1;

      .name {
        max-width: 120px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-size: 15px;
        font-weight: 700;
        color: #42495C;
        padding: 3px 0px 0px 0px;
      }

      .icon-phone {
        position: relative;
        left: -2px;
        font-size: 14px;
        color: #878a94;
        padding: 3px 0px 5px 0px;

        .under-phone {
          color: #aaa;
          font-size: 13px;
        }
      }

      .relieve {
        display: inline-block;
        font-size: 12px;
        color: #f3f3f3;
        padding: 4px 8px 3px 8px;
        border-radius: 4px;
        background-color: #999;
        letter-spacing 1px;
      }
    }
  }

  .right-abslute-down {
    position: absolute;
    right: 12px;
    bottom: 12px;
    z-index: 5;

    .icon-jiantou-copy {
      color: #9a9a9a;
      font-size: 18px;
      padding: 6px;
    }
  }
}
</style>



